<template>
  <v-form ref="form">
    <v-container class="fill-height">
      <v-responsive class="d-flex align-center text-center">
        <p class="pb-10"></p>
        <v-row dense>
          <v-col :cols="3"></v-col>
          <v-col :cols="6">
            <v-card class="mb-10">
              <v-card-title
                class="subheading font-weight-bold align-end text-right"
              >
                Login
              </v-card-title>

              <v-divider></v-divider>
              <v-row>
                <v-col :cols="1"></v-col>
                <v-col :cols="10">
                  <v-text-field
                    class="pt-10"
                    v-model="username"
                    label="Username"
                    v-on:keyup.enter="login"
                  ></v-text-field>
                </v-col>
                <v-col :cols="1"></v-col>
                <v-col :cols="1"></v-col>

                <v-col :cols="10">
                  <v-text-field
                    v-model="password"
                    label="Password"
                    type="password"
                    v-on:keyup.enter="login"
                  ></v-text-field>
                </v-col>
                <v-col :cols="1"></v-col>

                <v-divider></v-divider>
                <v-col :cols="5"></v-col>

                <v-col :cols="2">
                  <v-btn
                    class="align-center text-center justify-center"
                    @click="login"
                  >
                    login
                  </v-btn>
                </v-col>
                <v-col :cols="5"></v-col>
              </v-row>
            </v-card>
          </v-col>
          <v-col :cols="3"></v-col>
        </v-row>
      </v-responsive>
    </v-container>
  </v-form>
</template>

<script setup>
import { ref, defineEmits, onMounted } from 'vue';
import { useRouter } from 'vue-router/composables';

const router = useRouter();
const username = ref('');
const password = ref('');
const message = ref('');

const emit = defineEmits(['updateHeader']);

onMounted(() => {
  emit('updateHeader', window.sessionStorage.getItem('jwt'));
});

async function login() {
  const result = await fetch(`${process.env.VUE_APP_API_URL}/login`, {
    method: 'POST',
    body: JSON.stringify({
      username: username.value,
      password: password.value,
    }),
    headers: {
      'Content-Type': 'application/json',
    },
  }).finally(() => ({
    //
  }));
  const loginResults = await result.json();

  if (loginResults.token) {
    window.sessionStorage.setItem('jwt', JSON.stringify(loginResults));
    router.push({ name: 'main' });
  } else {
    username.value = '';
    password.value = '';
    message.value = loginResults.message;
  }
}
</script>
