<script>
/* eslint-disable vue/valid-v-slot */
/* eslint-disable no-debugger */
export default {
  name: 'BaseContent',
};
</script>

<script setup>
import { ref, onMounted } from 'vue';
import ProgressBar from '../components/ProgressBar';
import { useRouter } from 'vue-router/composables';

const router = useRouter();
const wtc_dates = ref([]);
const filterDates = ref([]);
const search = ref('');
const loading = ref(false);

onMounted(async () => {
  window.scrollTo(0, 0);

  wtc_dates.value = await apiData({
    url: '/openseats',
    params: {},
    method: 'GET',
  });
  filterDates.value = await apiData({
    url: '/dates',
    params: {},
    method: 'GET',
  });
});

async function apiData(params) {
  loading.value = true;
  const jwtToken = JSON.parse(window.sessionStorage.getItem('jwt'));
  if (!jwtToken) {
    loading.value = false;
    router.push({ path: '/login' });
    return null;
  }

  if (jwtToken) {
    const fetchObj = {
      method: params.method,
      headers: {
        'Content-Type': 'application/json',
        Authorization: `Bearer ${jwtToken.token}`,
      },
    };
    if (params.method !== 'GET') {
      fetchObj.body = JSON.stringify(params.params);
    }

    const result = await fetch(
      `${process.env.VUE_APP_API_URL}${params.url}`,
      fetchObj
    )
      .then((responce) => {
        if (responce.status === 403 || responce.status === 401) {
          router.push({ path: '/login' });
          return null;
        }
        return responce;
      })
      .catch((err) => {
        console.error(err);
      })
      .finally(() => (loading.value = false));

    return result.json();
  }
}

const sortBy = ref('');
const sortDesc = ref(false);
function setSearch(val) {
  search.value = val;
  sortBy.value = 'time_slot';
}

const headers = [
  { text: 'Day', value: 'displayDay' },
  { text: 'Display', value: 'displayValue' },
  { text: 'Available Seats', value: 'availableSeats' },
];
</script>

<template>
  <v-container class="">
    <v-row dense>
      <v-col :cols="12">
        <div
          v-if="loading"
          class="d-flex align-center justify-center pt-10 pb-5"
        >
          <ProgressBar />
        </div>
        <div v-else>
          <v-card outlined shaped>
            <v-card-title>
              <v-col :cols="6">Available Seats</v-col>
              <v-col :cols="6">
                <v-text-field
                  v-model="search"
                  append-icon="mdi-magnify"
                  label="Search"
                  single-line
                  hide-details
                ></v-text-field>
              </v-col>
            </v-card-title>
            <v-data-table
              :cols="6"
              loading-text="Loading... Please wait"
              :search="search"
              :sort-by.sync="sortBy"
              :sort-desc.sync="sortDesc"
              :headers="headers"
              :items="wtc_dates"
              :items-per-page="25"
              class="elevation-1"
            >
              <template v-slot:top>
                <v-toolbar flat>
                  <div>
                    <span v-for="dateObj in filterDates" :key="dateObj.search">
                      <v-btn color="yellow" @click="setSearch(dateObj.search)">
                        {{ dateObj.button }}
                      </v-btn>
                      <v-divider class="mx-4" inset vertical></v-divider>
                    </span>
                  </div>
                </v-toolbar>
              </template>

              <template v-slot:item.availableSeats="{ item }">
                <div class="text-right pr-10">
                  <strong>{{ item.availableSeats }}</strong>
                </div>
              </template>
            </v-data-table>
          </v-card>
        </div>
      </v-col>
    </v-row>
  </v-container>
</template>

<style></style>
