<script>
/* eslint-disable vue/valid-v-slot */
/* eslint-disable no-debugger */
export default {
  name: 'BaseContent',
};
</script>

<script setup>
import { ref, onMounted, nextTick } from 'vue';
import ProgressBar from '../components/ProgressBar';
import { useRouter } from 'vue-router/composables';

const router = useRouter();
const reservations = ref([]);
const wtc_dates = ref([]);

const search = ref('');
const loading = ref(false);

onMounted(async () => {
  window.scrollTo(0, 0);

  wtc_dates.value = await apiData({
    url: '/dates',
    params: {},
    method: 'GET',
  });
  reservations.value = await apiData({
    url: '/reservations',
    params: {},
    method: 'GET',
  });
});

async function apiData(params) {
  loading.value = true;
  const jwtToken = JSON.parse(window.sessionStorage.getItem('jwt'));
  if (!jwtToken) {
    loading.value = false;
    router.push({ path: '/login' });
    return null;
  }

  if (jwtToken) {
    const fetchObj = {
      method: params.method,
      headers: {
        'Content-Type': 'application/json',
        Authorization: `Bearer ${jwtToken.token}`,
      },
    };
    if (params.method !== 'GET') {
      fetchObj.body = JSON.stringify(params.params);
    }

    const result = await fetch(
      `${process.env.VUE_APP_API_URL}${params.url}`,
      fetchObj
    )
      .then((responce) => {
        if (responce.status === 403 || responce.status === 401) {
          router.push({ path: '/login' });
          return null;
        }
        return responce;
      })
      .catch((err) => {
        console.error(err);
      })
      .finally(() => (loading.value = false));

    return result.json();
  }
}

const headers = [
  {
    text: 'Name',
    align: 'start',
    sortable: true,
    value: 'name',
  },
  { text: 'Email', value: 'email' },
  { text: 'Date', value: 'formattedDate' },
  { text: 'Adults', value: 'adult_seats' },
  { text: 'Child', value: 'child_seats' },
  { text: 'Total', value: 'reserved_seats' },
  { text: 'Phone', value: 'phone' },
  { text: 'Actions', value: 'actions', sortable: false },
];

function formatPhone(phoneNumberString) {
  phoneNumberString = phoneNumberString.replaceAll('(', '');
  phoneNumberString = phoneNumberString.replaceAll(')', '');
  phoneNumberString = phoneNumberString.replaceAll('+', '');
  phoneNumberString = phoneNumberString.replaceAll(' ', '');
  phoneNumberString = phoneNumberString.replaceAll('-', '');

  if (phoneNumberString.length === 11)
    phoneNumberString = phoneNumberString.substring(1, 11);
  if (phoneNumberString.length === 10) {
    phoneNumberString = `(${phoneNumberString.substring(
      0,
      3
    )}) ${phoneNumberString.substring(3, 6)}-${phoneNumberString.substring(
      6,
      10
    )}`;
  }

  return phoneNumberString;
}

const snack = ref(false);
const snackColor = ref('');
const snackText = ref('');
// methods(()=> {

//     });
const dialog = ref(false);
const dialogDelete = ref(false);
const editedItem = ref({
  name: '',
  email: '',
  adult_seats: 0,
  child_seats: 0,
});
const defaultItem = ref({
  name: '',
  email: '',
  adult_seats: 0,
  child_seats: 0,
});

const editedIndex = ref(-1);

function editItem(item) {
  editedIndex.value = reservations.value.indexOf(item);
  editedItem.value = Object.assign({}, item);
  dialog.value = true;
}

function deleteItem(item) {
  editedIndex.value = reservations.value.indexOf(item);
  editedItem.value = Object.assign({}, item);
  dialogDelete.value = true;
}

function deleteItemConfirm(data) {
  apiData({
    url: '/reservations',
    params: data,
    method: 'DELETE',
  });

  reservations.value.splice(editedIndex.value, 1);
  closeDelete();
}

function close() {
  dialog.value = false;
  nextTick(() => {
    editedItem.value = Object.assign({}, defaultItem.value);
    editedIndex.value = -1;
  });
}

function closeDelete() {
  dialogDelete.value = false;
  nextTick(() => {
    editedItem.value = Object.assign({}, defaultItem.value);
    editedIndex.value = -1;
  });
}

function save(data) {
  // console.log(data);

  editedItem.value.adult_seats = +editedItem.value.adult_seats;
  editedItem.value.child_seats = +editedItem.value.child_seats;
  editedItem.value.reserved_seats =
    +editedItem.value.adult_seats + +editedItem.value.child_seats;

  //postApiData(data);
  apiData({
    url: '/reservations',
    params: data,
    method: 'POST',
  });

  if (editedIndex.value > -1) {
    Object.assign(reservations.value[editedIndex.value], editedItem.value);

    close();
  }
}
const sortBy = ref('');
const sortDesc = ref(false);

function setSearch(val) {
  search.value = val;
  sortBy.value = 'time_slot';
}
</script>

<template>
  <v-container class="">
    <v-row dense>
      <v-col :cols="12">
        <div
          v-if="loading"
          class="d-flex align-center justify-center pt-10 pb-5"
        >
          <ProgressBar />
        </div>
        <div v-else>
          <v-card outlined shaped>
            <v-card-title>
              <v-col :cols="6">Reservations</v-col>
              <v-col :cols="6">
                <v-text-field
                  v-model="search"
                  append-icon="mdi-magnify"
                  label="Search"
                  single-line
                  hide-details
                ></v-text-field>
              </v-col>
            </v-card-title>
            <v-data-table
              loading-text="Loading... Please wait"
              :search="search"
              :sort-by.sync="sortBy"
              :sort-desc.sync="sortDesc"
              :headers="headers"
              :items="reservations"
              :items-per-page="25"
              class="elevation-1"
            >
              <template v-slot:top>
                <v-toolbar flat>
                  <div>
                    <span v-for="dateObj in wtc_dates" :key="dateObj.search">
                      <v-btn color="amber" @click="setSearch(dateObj.search)">
                        {{ dateObj.button }}
                      </v-btn>
                      <v-divider class="mx-4" inset vertical></v-divider>
                    </span>
                  </div>
                  <v-dialog v-model="dialog" max-width="500px">
                    <v-card>
                      <v-card-title>
                        <span class="text-h5">Edit</span>
                      </v-card-title>

                      <v-card-text>
                        <v-container>
                          <v-row>
                            <v-col cols="12" sm="6" md="6">
                              <v-text-field
                                v-model="editedItem.name"
                                label="Name"
                              ></v-text-field>
                            </v-col>
                            <v-col cols="12" sm="6" md="6">
                              <v-text-field
                                v-model="editedItem.email"
                                label="Email"
                              ></v-text-field>
                            </v-col>

                            <v-col cols="12" sm="6">
                              <v-text-field
                                v-model="editedItem.adult_seats"
                                label="Adults"
                                type="number"
                              ></v-text-field>
                            </v-col>
                            <v-col cols="12" sm="6">
                              <v-text-field
                                v-model="editedItem.child_seats"
                                label="Child"
                                type="number"
                              ></v-text-field>
                            </v-col>
                          </v-row>
                        </v-container>
                      </v-card-text>

                      <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn color="blue darken-1" text @click="close()">
                          Cancel
                        </v-btn>
                        <v-btn
                          color="blue darken-1"
                          text
                          @click="save(editedItem)"
                        >
                          Save
                        </v-btn>
                      </v-card-actions>
                    </v-card>
                  </v-dialog>
                  <v-dialog v-model="dialogDelete" max-width="500px">
                    <v-card>
                      <v-card-title class="text-h5"
                        >Are you sure you want to delete this
                        reservation?</v-card-title
                      >
                      <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn color="blue darken-1" text @click="closeDelete()"
                          >Cancel</v-btn
                        >
                        <v-btn
                          color="blue darken-1"
                          text
                          @click="deleteItemConfirm(editedItem)"
                          >OK</v-btn
                        >
                        <v-spacer></v-spacer>
                      </v-card-actions>
                    </v-card>
                  </v-dialog>
                </v-toolbar>
              </template>
              <template v-slot:item.actions="{ item }">
                <v-icon class="mr-2" @click="editItem(item)">
                  mdi-pencil
                </v-icon>
                <v-icon @click="deleteItem(item)"> mdi-delete </v-icon>
              </template>

              <template v-slot:item.phone="{ item }">
                <div class="text-right">
                  {{ formatPhone(item.phone) }}
                </div>
              </template>
            </v-data-table>

            <v-snackbar v-model="snack" :timeout="3000" :color="snackColor">
              {{ snackText }}

              <template v-slot:action="{ attrs }">
                <v-btn v-bind="attrs" text @click="snack = false">
                  Close
                </v-btn>
              </template>
            </v-snackbar>
          </v-card>
        </div>
      </v-col>
    </v-row>
  </v-container>
</template>

<style></style>
